import React, { Component } from 'react'
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import testImage from '../../assets/test.png'
import { Button, CardActionArea, CardActions } from '@mui/material';
import PlayCircleIcon from '@mui/icons-material/PlayCircle';
import Avatar from '@mui/material/Avatar';
import "../../css/VideoCard.css"


export default class VideoCard extends Component {

  constructor(props){
    super(props)
    // console.log(props)
    this.state= {
      id:this.props.CardData.id,
      Poster:this.props.CardData.poster,
      Url:this.props.CardData.url,
      HeadingText:this.props.CardData.HeadingText,
      ParagraphText:this.props.CardData.ParagraphText,
      SendtoPage:this.props.CardData.to,
      avatar:this.props.CardData.avatar,
      playAndStopVideo:this.props.playAndStopVideo
    }
  }

  render() {
    const {id,HeadingText,ParagraphText,SendtoPage,avatar} = this.state;
      // console.log(this.state)  
    // console.log(HeadingText)
      // console.log(ParagraphText)
    return (
      <Box style={{p:2,minHeight:'463px',maxWidth:'412px'}}>
        <Card  key={id} sx={{height:'460px'}}>
          <CardActionArea>
          <Box sx={{height:'200px',width:'100%'}} className="video-wrapper" onClick={()=>{this.state.playAndStopVideo(id)}}>
            <Box sx={{width:'100%',background:'green'}}className="video-container" id="video-container">
              <video  id={id} loop   preload="metadata" poster="//cdn.jsdelivr.net/npm/big-buck-bunny-1080p@0.0.6/poster.jpg">
                <source src="//cdn.jsdelivr.net/npm/big-buck-bunny-1080p@0.0.6/video.mp4" type="video/mp4"/>
              </video>
              <Box style={{position:'absolute',width:'100%'}}>
                <Box style={{display:'flex',justifyContent:'center'}}>
                  <PlayCircleIcon id={"Button"+id} sx={{background:'white',borderRadius:'100px',width:'72px',height:'72px',color:'#682CEA'}}  >
                  </PlayCircleIcon>
                </Box>
              </Box>
              {avatar!=='' && 
              (<Box sx={{height:'100%',position:'absolute',display:'flex',alignItems:'flex-end',padding:2}}>
                  <Avatar alt="Travis Howard" src={avatar} sx={{border:"3px solid #682CEA",justifyContent:'center',alignItems:'center',float:'bottom',width:'61px',height:'61px'}} />
              </Box>)}
            </Box>
          </Box> 
          <Box sx={{mt:7}}>
            <CardContent >
              <Typography style={{fontWeight:'bold'}} gutterBottom variant="h5" component="div">
                {HeadingText}
              </Typography>
              <Typography variant="body2" sx={{fontSize:'16px'}} color="text.secondary">    
                {ParagraphText}
              </Typography>
            </CardContent>
            <CardActions sx={{justifyContent:'center',mt:1}}>
              <Button  size="small" color="primary" to={SendtoPage}>
                See more experiences 
              </Button>
            </CardActions>
          </Box>
          </CardActionArea>
        </Card>
      </Box>
    )
  }
}


