import React, { Component } from 'react'
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import "../../css/VideoCard.css";
import testImage from '../../assets/test.png'
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { styled } from '@mui/material/styles';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import IconButton from '@mui/material/IconButton';
import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';


export default class ARExperienceCard extends Component {

  constructor(props){
      super(props)
      this.state = {
          ARExperienceMenu : null,
          ARExperienceOptions : [
            'Edit',
            'Delete'],
          // open:Boolean(this.ARExperienceMenu),
          open:false,
          cardData:props.CardData,
          playAndStopVideo:props.playAndStopVideo,
          BorderLinearProgress : styled(LinearProgress)(({ theme }) => ({
            height: 5,
            borderRadius: 5,
            [`&.${linearProgressClasses.colorPrimary}`]: {
              backgroundColor: theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
            },
            [`& .${linearProgressClasses.bar}`]: {
              borderRadius: 5,
              backgroundColor: theme.palette.mode === 'light' ? '#1a90ff' : '#308fe8',
            },
          })),
      }
  }

  componentDidMount() {
      const video = document.getElementById("video-wrapper");  
  };

  OpenARExperienceMenu = (event) => {
    console.log(event);
    this.setState({ARExperienceMenu:event.currentTarget,open:true});
  };

  CloseARExperienceMenu = () => {
    this.setState({ARExperienceMenu:null,open:false});
  }

  render() {
    const BorderLinearProgress = this.state.BorderLinearProgress
    const progress = 75; // in ragen from 1 to 100
    const ITEM_HEIGHT= 48;
    return (
    <Card sx={{ display: 'flex',width: '500px',height: '124px',boxShadow: '1px 1px 6px 5px #888888'}}>
      <CardMedia
        component="img"
        sx={{ width: 151,p:2 }}
        image={testImage}
        alt="Live from space album cover"
      />
      <Box sx={{ display: 'flex',width:"100%", flexDirection: 'column'}}>
        <CardContent sx={{ flex: '1 0 auto' }}>
          
          <Box style={{m:0,p:0}}>
            <Typography component="div"  sx={{display:'inline',fontSize:'18px',fontWeight:'bold'}}>
            Collect 1808Collect 1808
            </Typography>
            <IconButton 
              sx={{float:'right'}} 
              id="long-button"
              onClick={this.OpenARExperienceMenu}
              aria-controls={this.state.open ? 'long-menu' : undefined}
              aria-expanded={this.state.open ? 'true' : undefined}
              aria-haspopup="true"
            >
              <MoreVertIcon />
            </IconButton>
            <Menu
                id="long-menu"
                MenuListProps={{
                  'aria-labelledby': 'long-button',
                }}
                anchorEl={this.state.ARExperienceMenu}
                open={this.state.open}
                onClose={ this.CloseARExperienceMenu}
                PaperProps={{
                  style: {
                    maxHeight: ITEM_HEIGHT * 4.5,
                    width: '20ch'
                  },
                }}
              >
                {this.state.ARExperienceOptions.map((option) => (
                  <MenuItem key={option} onClick={this.CloseARExperienceMenu}>
                    {option}
                  </MenuItem>
                ))}
              </Menu>


          </Box>
          <Box sx={{width:"100%",display:'flex'}}>
            
            <Box style={{width:"80%"}}>
              <Typography  color="text.secondary" component="div" sx={{fontSize:'13px'}}>
                Games,<span style={{color:'grey',fontSize:'10px',marginLeft:'2px'}}>2022/08/18</span>
              </Typography>
              <Typography variant="subtitle2" sx={{mt:1}}>
                {progress}%
              </Typography>
              <Box style={{width:"70%"}}>
                <BorderLinearProgress  variant="determinate" value={progress} />
              </Box>
              
            </Box>
            <Box style={{marginTop:'-8px',width:"30%",display:"flex",justifyContent:'center',flexDirection:'column',alignItems:'center'}}>
              <Typography sx={{color:"#682CEA",fontSize:'32px',fontWeight:'bold',p:0,m:0}}>
                56
              </Typography>
              <Typography variant="subtitle2">
              Total views
              </Typography>
            </Box>
          </Box>
          
        </CardContent>
      </Box>
 
    </Card>
    )
  }
}
