import React, { Component } from 'react'
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';


export default class CreateExperienceCard extends Component {
    constructor(props){
        super(props)
        this.state = {
            data:props.data,
            cardStyles : {
                background: "#f00 !important",
                '&:hover': {
                    background: "#f331 !important",
                }
            }
        }
    }

    render() {
        const {data} = this.state;
        return (
          <Box >
                <Card className={this.state.cardStyles} elevation={0} sx={{
                    background:'#E9F5FF',
                    width: '456px',height: '182px', display:'flex',m:2,alignItems:'center',
                    '&:hover': {
                            boxShadow:6,
                        }}}>
                    <Box sx={{position:'absolute',display:'flex-inline'}}>
                    <CardMedia
                        component="img"
                        height='100%'
                        image={data.img}
                    />
                    </Box>
                    <Box sx={{fontSize:'18px',display:'flex',justifyContent:'right',pr:4,alignItems:'center',fontWeight:'bold',zIndex:10,height:'100%',width:'100%'}}>
                    {data.name}
                    </Box>
                </Card>
          </Box>
        )
      }
}
