import React, { Component } from 'react'
import SettingsIcon from '@mui/icons-material/Settings';
import LogoutIcon from '@mui/icons-material/Logout';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Badge from '@mui/material/Badge';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
// import Link from '@mui/material/Link';
import {Link} from 'react-router-dom'
import Button from '@mui/material/Button';
import AddCircleRoundedIcon from '@mui/icons-material/AddCircleRounded';
import ArrowRightIcon from '@mui/icons-material/ArrowRight';
import HomeIcon from '@mui/icons-material/Home';
import DashboardIcon from '@mui/icons-material/Dashboard';
import TokenIcon from '@mui/icons-material/Token';
import PieChartIcon from '@mui/icons-material/PieChart';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import KeyIcon from '@mui/icons-material/Key';
import ThreeDRotationIcon from '@mui/icons-material/ThreeDRotation';
import PhotoLibraryIcon from '@mui/icons-material/PhotoLibrary';
import InsightsIcon from '@mui/icons-material/Insights';
import InfoIcon from '@mui/icons-material/Info';
import Collapse from '@mui/material/Collapse';
import BoltRoundedIcon from '@mui/icons-material/BoltRounded';
import NotificationsNoneRoundedIcon from '@mui/icons-material/NotificationsNoneRounded';
import * as Icon from '@mui/material/Icon';
import Tooltip from '@mui/material/Tooltip';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import MuiDrawer from '@mui/material/Drawer';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import vossleLogo from '../../assets/logo.png';
import '../../css/DrawerAndNavbar.css'


import {createTheme, ThemeProvider} from '@mui/material/styles';
import { ArrowRight } from '@mui/icons-material';

class DrawerAndNavbar extends Component {

  constructor(props) {
    super(props);
    
    this.state = {
      theme:createTheme({
        palette:{
          primary:{
            main:'#682CEA',
            darker:'#682CEA'
          }
        },
        '@global': {
          '*::-webkit-scrollbar': {
            width: '0.4em'
          },
          '*::-webkit-scrollbar-track': {
            '-webkit-box-shadow': 'inset 0 0 6px rgba(0,0,0,0.00)'
          },
          '*::-webkit-scrollbar-thumb': {
            backgroundColor: 'rgba(0,0,0,.1)',
            outline: '1px solid slategrey'
          }
        }
      }),
      open : false,
      anchorElUser:null,
      anchorElNav:null,
      userProfile: {name:'rishabh Jaiswal queppelin',email:'myemail@gmail.com'},
      profileOptions : [{name:"Dashboard",route:"/Dashboard",icon:<SettingsIcon/>}, {name:"Logout",route:"/Profile",icon:<LogoutIcon/>}],
      drawerWidth : 298,
      direction:'rtl',
      arExperienceCollapse:false,
      DrawerHeader : styled('div')(({ theme }) => ({
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
      })),
      handleOpenUserMenu : (event) => {
        this.setState({anchorElUser:event.currentTarget})
      },
      handleCloseUserMenu : () => {
        this.setState({anchorElUser:null})
      },
      handleDrawerOpen : () => {
        this.setState({direction:'ltr'})
        this.setState({ open : true })
      },
      handleDrawerClose : () => {
        this.setState({direction:'rtl'})
        this.setState({open:false});
      },
      AppBar : styled(MuiAppBar, {
        shouldForwardProp: (prop) => prop !== 'open',
      })(({ theme, open }) => ({
        
      })),
      Item : styled(Paper)(({ theme }) => ({
        backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
        ...theme.typography.body2,
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
      })),
      Drawer : styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
        ({ theme, open }) => ({
          width: this.state.drawerWidth,
          flexShrink: 0,
          whiteSpace: 'nowrap',
          boxSizing: 'border-box',
          ...(open && {
            ...this.openedMixin(theme),
            '& .MuiDrawer-paper': this.openedMixin(theme),
          }),
          ...(!open && {
            ...this.closedMixin(theme),
            '& .MuiDrawer-paper': this.closedMixin(theme),
          }),
        }),
      ),
      selectedItemId:1,
      DrawerTopItems:[{
        id:1,
        name:'Home',
        icon:<HomeIcon/>,
        accordian:false,
        to:'/'
      },{
        id:2,
        name:'Dashboard',
        icon:<DashboardIcon/>,
        accordian:false,
        to:'/dashboard'
      },{
        id:3,
        name:'AR Experiences',
        icon:<TokenIcon/>,
        accordian:true,
        children:[{
            name:'Markerless',
            to:'/arExperiences'
          },{
            name:'Face Detection',
            to:'/arExperiences'
          },{
            name:'Games',
            to:'/arExperiences'
          },{
            name:'Workflow',
            to:'/arExperiences'
          },{
            name:'All Experiences',
            to:'/arExperiences'
          }
        ]
      },{
        id:4,
        name:'Analytics',
        icon:<PieChartIcon/>,
        accordian:false,
        to:'/analytics'
      },{
        id:5,
        name:'Payments',
        icon:<AccountBalanceWalletIcon/>,
        accordian:false,
        to:'/payments'
      },{
        id:6,
        name:'Get API Key',
        icon:<KeyIcon/>,
        accordian:false,
        to:'/keys'
      },{
        id:7,
        name:'Collections',
        icon:<ThreeDRotationIcon/>,
        accordian:false,
        to:'/collections'
      }],
      DrawerMiddleItems:[{
        id:8,
        name:'Library',
        icon:<PhotoLibraryIcon/>,
        accordian:true,
        to:'/library'
      },{
        id:9,
        name:'Insights', 
        icon:<InsightsIcon/>,
        accordian:true,
        to:'/insignts'
      }],

      DrawerBottomItems:[{
          id:10,
          name:'Settings',
          icon:<SettingsIcon/>,
          accordian:false,
          to:'/settings'
        },{
          id:11,
          name:'Help & getting started',
          icon:<InfoIcon/>,
          accordian:false,
          to:'/help'
        }
      ],
      handleItemsClick:(event)=>{
        console.log("clicked on Item",event)
      },
      handleARExperienceClick:(id)=>{
          this.state.handleSelectPage(id)
          if(this.state.arExperienceCollapse===true)
            this.setState({arExperienceCollapse:false})
          else
            this.setState({arExperienceCollapse:true})
      },
      handleSelectPage:(id)=>{
        if(this.state.selectedItemId !== id){
          let item = document.getElementById(this.state.selectedItemId).style;
          item.background = 'transparent';
          item.background = `linear-gradient(to left, transparent 50%, #682CEA  50%) right`;
          item.backgroundSize=  `200%`;
          item.transition = `.59s ease-out !important`;
          item.color = 'black';
      
          document.getElementById(id).style.background = '#682CEA';
          document.getElementById(id).style.color = 'white';
          this.setState({selectedItemId:id});
          // console.log(id);
        }
      },
    }

  }


  openedMixin = (theme) => ({
    width: this.state.drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
    overflowX: 'hidden',
  });
  
  closedMixin = (theme) => ({
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: `calc(${theme.spacing(7)} + 1px)`,
    [theme.breakpoints.up('sm')]: {
      width: `calc(${theme.spacing(8)} + 1px)`,
    },
  });

  render(){
    const {DrawerBottomItems,DrawerMiddleItems,DrawerTopItems,drawerWidth,direction,DrawerHeader,AppBar,Drawer,Item} = this.state;
    return (

      <Box sx={{ display: 'flex', height:'100%'}}>
        <AppBar style={{ background:'white',padding:'10px',fontFamily:'Noto Sans, Regular',width:`calc(100% - calc(56px + 1px))`,height:'77px'}} position="fixed" open={this.state.open}>
          <Toolbar sx={{display:'flex', m:'0 !important',p:'0 !important',height:'100%'}}>
            
            <Box sx={{ flexGrow: 0}} style={{width:'100%',height:'100%'}}>
              <Grid container style={{height:'100%',marginTop:'11px'}}>
                
                <Grid  className="my-grid" item xs={5} sm={9} md={7} lg={6} xl={6}>
                  <span style={{color:'black',fontSize:'24px',marginLeft:'27px'}}>
                    Dashboard
                  </span>
                </Grid>
                
                <Grid  item xs={0} sm={0} md={0} lg={3} xl={3}>
                      <Button className="extra-large-screen-only" style={{background:'#37BE70',width:'76%',color:'white',borderRadius:'20px',fontSize:'14px'}}> 
                        <Typography style={{fontSize:'14px'}}>Free Trial Started | 30 days left</Typography>
                      </Button>
                </Grid>

                <Grid  item xs={0} sm={0} md={3} lg={2} xl={2} >
                      <Button  className='large-screen-only'  startIcon={<BoltRoundedIcon />} 
                          style={{background:'#1C7ED6',color:'white',width:'76%',borderRadius:'20px'}}
                       > 
                        <Typography sx={{display:'inline',fontSize:'16px'}}>Upgrade Now</Typography>
                      </Button>
                </Grid>
                
                <Grid item xs={7} sm={3}  md={2} lg={1} xl={1} >      
                  <Box  style={{background:'',width:"100%",display:'flex',height:'100%'}}>
                    <Grid container justify="flex-end" style={{height:'100%',alignItems:'right'}}>
                      
                      <Grid item xs={4} sm={4} md={4} lg={4} xl={4}>
                        <div style={{display:'flex',justifyContent:'center'}}>
                          <AddCircleRoundedIcon className="medium-screen-only"  sx={{color:'#FF821C',fontSize:"40px"}}/>
                        </div>
                      </Grid>
                      
                      <Grid  item xs={4} sm={4} md={4} lg={4} xl={4}>
                        <div style={{display:'flex',justifyContent:'center'}}>
                          <IconButton
                            size="large"
                            aria-label="show 17 new notifications"
                            color="inherit"
                          >
                            <ThemeProvider theme={this.state.theme}>

                              <Badge badgeContent={17}  color="primary" style={{color:'#682CEA'}} >
                                <NotificationsNoneRoundedIcon size='large' style={{color:"grey",fontSize:'32px'}}/>
                              </Badge>
                            </ThemeProvider>
                          </IconButton>
                        </div>
                      </Grid>

                      <Grid  item xs={4} sm={4} md={4} lg={4} xl={4} >
                      <div style={{display:'flex',justifyContent:'center',}}>
                          <Tooltip title="My Profile" >
                            <IconButton onClick={this.state.handleOpenUserMenu}>
                              <Avatar alt="Remy Sharp" src="/static/images/avatar/2.jpg" style={{width:'42px'}} />
                            </IconButton>
                          </Tooltip>
                        </div>
                      </Grid>
                      
                      
                    </Grid>
                  </Box>
                   
                    

                    
                </Grid>
              </Grid>
            </Box>
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={this.state.anchorElUser}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(this.state.anchorElUser)}
              onClose={this.state.handleCloseUserMenu}
            >
              <div  style={{background:'white',width:'200px'}}>
                <div className="arrow-up"></div>
                <div id="profile-menu">
                  <div>
                    <span>{this.state.userProfile.name}</span>
                    <br/>
                    <span>{this.state.userProfile.email}</span>
                  </div>

                  {this.state. profileOptions.map((setting) => {
                    let icon = setting.icon
                    return(
                    <MenuItem key={setting.name} onClick={this.state.handleCloseUserMenu} style={{padding:'10px',display:'flex'}}>
                      
                      <Typography textAlign="center">{setting.name}</Typography>
                      <div style={{alignItems:'right'}}>
                        {icon}
                      </div>  
                    </MenuItem>
                  )})}
                </div>
              </div>
            </Menu>
                
          </Toolbar>
        </AppBar>
        <Drawer className='Drawer' sx={{maxHeight:'200vh',background:'#F1F3F5'}} variant="permanent" open={this.state.open} >
          <Box sx={{background:'#F1F3F5',minHeight:'100vh'}}>
            <DrawerHeader style={{height:'76px'}}>
              {
                direction === 'rtl' ? (<div>
                </div>):(
                  <div style={{width:"100%",marginLeft:'15px',marginTop:'5px'}}>
                    <img src={vossleLogo} alt="Cinque Terre" height="46px" ></img>
                  </div>
                )
              }
              {
                direction === 'rtl'?
                  (<IconButton onClick={this.state.handleDrawerOpen}>
                    <ChevronRightIcon style={{height:"26px",width:'26px',color:'white',backgroundColor:'#682CEA',borderRadius:'50%'}} /> 
                  </IconButton>)
                :(
                  <IconButton onClick={this.state.handleDrawerClose}> 
                    <ChevronLeftIcon style={{height:"26px",width:'26px',color:'white',backgroundColor:'#682CEA',borderRadius:'50%'}} />
                  </IconButton>
                )
              }
            </DrawerHeader>
            <Box style={{minHeight:'450px'}} sx={{mt:'30px'}} >
                <List style={{fontFamily:'Noto Sans'}}>
                
                  {/* {['Home', 'Dashboard', 'AR Experiences', 'Analytics','Payments','Get API Key','Collections'].map((text, index) => ( */}

                  {DrawerTopItems.map((item, index) => (
                    <ListItem key={index}  
                    disablePadding 
                    sx={{color:'black',p:0,b:0,m:0}} component={Link} to={item.to} 
                    >
                      {
                        item.accordian === false
                        ?(<ListItemButton
                            sx={{
                              minHeight: 48,
                              justifyContent: this.state.open ? 'initial' : 'center',
                              px: 2.5,
                            }}
                            // id = "drawer-items"+${index}
                            className = "drawer-items"
                            onClick = {()=>this.state.handleSelectPage(item.id)}
                            id = {item.id}
                          >
                        <ListItemIcon
                          sx={{
                            minWidth: 0,
                            mr: this.state.open ? 3 : 'auto',
                            justifyContent: 'center',
                          }}
                        >
                          {item.icon}
                        </ListItemIcon>
                        <ListItemText primary={item.name} sx={{ opacity: this.state.open ? 1 : 0 }} />
                        </ListItemButton>):
                        (<div style={{width:'100%'}}>
                         
                         <ListItemButton
                                aria-controls="panel1a-content"
                                sx={{
                                  width:'100%',
                                  justifyContent: this.state.open ? 'initial' : 'initial',
                                  px: 2.5, 
                                  Py: 0,
                                  border:'0px !important',
                                  margin:'0px !important',
                                  width:'100%',
                                }}
                                    /* Old browsers */
                                className = "drawer-items"
                                id = {item.id}

                                onClick={()=>{this.state.handleARExperienceClick(item.id)}}>
                              <ListItemIcon
                                  sx={{
                                    minWidth: 0,
                                    mr: this.state.open ? 3 : 'auto',
                                    justifyContent: 'center',
                                    padding:'0 !important',
                                  }}
                                >

                                  {item.icon}
                                </ListItemIcon>
                          

                                <ListItemText 
                                  aria-controls="panel1a-content"
                                  id="panel1a-header"
                                  primary={item.name} sx={{p:0, opacity: this.state.open ? 1 : 0 ,display:'flex'}} 
                                />
                                
                                {this.state.open && (this.state.arExperienceCollapse ? <ExpandLess /> : <ExpandMore />)}
                            </ListItemButton>
                              <Collapse sx={{background:'white'}} in={this.state.arExperienceCollapse} timeout="auto" unmountOnExit>
                              <List component="div" sx={{p:1}} >
                                {
                                  item.children.map((childItem)=>{
                                    return(
                                      <ListItemButton sx={{ pl: 7 }} component={Link} to={childItem.to} >
                                        <ListItemText primary={childItem.name} />
                                      </ListItemButton>
                                    )
                                  })
                                }
                                {/* <ListItemButton sx={{ pl: 4 }}>
                                  <ListItemText primary="Starred" />
                                </ListItemButton>
                                <ListItemButton sx={{ pl: 4 }}>
                                  <ListItemText primary="Starred" />
                                </ListItemButton>
                                <ListItemButton sx={{ pl: 4 }}>
                                  <ListItemText primary="Starred" />
                                </ListItemButton> */}
                              </List>
                            </Collapse>
                        </div>

                      
                          //       <ListItemText 
                          //        aria-controls="panel1a-content"
                          //        id="panel1a-header"
                          //       primary={item.name} sx={{background:'red', p:0, opacity: this.state.open ? 1 : 0 ,display:'flex'}} 
                          //       />
                                
                          //   </AccordionSummary>
                          //   </ListItemButton>
                            
                          //   <AccordionDetails sx={{background:'white',m:'0'}}>
                          //     <Typography sx={{p:1}}>
                          //       Markerless
                          //     </Typography>
                          //     <Typography sx={{p:1}}>
                          //       Face Detection
                          //     </Typography>
                          //     <Typography sx={{p:1}}>
                          //       Games
                          //     </Typography>
                          //     <Typography sx={{p:1}}>
                          //         Workflow
                          //     </Typography>
                          //     <Typography sx={{p:1}}>
                          //       All Experiences
                          //     </Typography>
                          //   </AccordionDetails>
                          // </Accordion>)
                        )
                      }
                    </ListItem>
                  ))}
                </List>
            </Box>
            <Box style={{height:'200px'}}>
                <List style={{fontFamily:'Noto Sans, Regular'}}>
                  {DrawerMiddleItems.map((item, index) => (
                    <ListItem key={Item.name} disablePadding sx={{ display: 'block' }}>
                      <ListItemButton
                        sx={{
                          minHeight: 48,
                          justifyContent: this.state.open ? 'initial' : 'center',
                          px: 2.5,
                        }}
                        className = "drawer-items"
                        onClick = {()=>this.state.handleSelectPage(item.id)}
                        id = {item.id}
                      >
                        <ListItemIcon
                          sx={{
                            minWidth: 0,
                            mr: this.state.open ? 3 : 'auto',
                            justifyContent: 'center',
                          }}
                        >
                          {item.icon}
                        </ListItemIcon>
                        <ListItemText primary={item.name} sx={{ opacity: this.state.open ? 1 : 0 }} />
                      </ListItemButton>
                    </ListItem>
                  ))}
                </List>
            </Box>
            <Box style={{justifyContent:'center',alignItems:'bottom',height:'calc(30%-76px)'}}>
              <List style={{fontFamily:'Noto Sans, Regular',display:'flex',alignItems:'center'}}>
                <div style={{width:"100%"}}>
                  {DrawerBottomItems.map((Item, index) => (
                    <ListItem key={Item.name} disablePadding sx={{ display: 'block' }}>
                      <ListItemButton
                        sx={{
                          minHeight: 48,
                          justifyContent: this.state.open ? 'initial' : 'center',
                          px: 2.5,
                        }}
                        className = "drawer-items"
                        onClick = {()=>this.state.handleSelectPage(Item.id)}
                        id = {Item.id}
                      >
                        <ListItemIcon
                          sx={{
                            minWidth: 0,
                            mr: this.state.open ? 3 : 'auto',
                            justifyContent: 'center',
                          }}
                        >
                          {Item.icon}
                        </ListItemIcon>
                        <ListItemText primary={Item.name} sx={{ opacity: this.state.open ? 1 : 0 }} />
                      </ListItemButton>
                      {index+1 === DrawerBottomItems.length?'':<Divider style={{width:'100%'}}/>}
                    </ListItem>
                    
                  ))}
                </div>
              </List>
            </Box>
          </Box>
        </Drawer>
      
      </Box>
    );
  }
}

export default DrawerAndNavbar;
