import React, { Component } from 'react'
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import testImage from '../../assets/test.png';
import Typography from '@mui/material/Typography';
import KeyIcon from '@mui/icons-material/Key';
import Key1 from '../../assets/Key1.png';
import { DataArray } from '@mui/icons-material';


export default class Keys extends Component {
  
  constructor(props){
    super(props)
    // console.log(props)
      this.state = {
        data:props.data,
        cardStyles : {
          background: "#f00 !important",
          '&:hover': {
            background: "#f331 !important",
          }
        },
        MouseOnCard:(id)=>{
          let cardImg = document.getElementById(id);
          console.log("on card",cardImg)
          cardImg.style.marginLeft='30px';
          cardImg.style.color='white';

        },
        MouseOutFromCard:(id)=>{
          let cardImg = document.getElementById(id);
          console.log("not on card",cardImg)
          cardImg.style.marginLeft='0px';
          cardImg.style.color='black';
        }
      }
    }

  render() {
    const {data}=  this.state;
    console.log(data)
    return (
         <Box className="keyCard" >
            <Card  className="CardForStyling" elevation={0} 
            
                onMouseOver={() => this.state.MouseOnCard('KeyImage'+data.id)}
                onMouseOut={() => this.state.MouseOutFromCard('KeyImage'+data.id)}
                sx={{
                // background:'#9090A554',
                border:'1px solid grey',
                borderRadius:'10px',

                width:'212px',height:'265px',m:2,alignItems:'center',
                '&:hover': {
                        boxShadow:6,
                        backgroundColor:'#682CEA',
                        color:'white'
                    }}}>
                    <div style={{marginTop:'10px',display:'flex',justifyContent:'center',alignItems:'center'}}>
                      <CardMedia
                          component="img"
                          sx={{width:"155px",height:'160px','&:hover': {
                                // ml:'15px'
                              }}}
                          image={Key1}
                          id = {'KeyImage'+data.id}
                          
                      />
                    </div>
                <Box sx={{
                  height:'100px',
                  float:'bottom',
                  width:"100%",
                  textAlign:'center'
                }}
                >
                  <Typography sx={{fontSize:'18px',p:0.5,fontWeight:'550'}}>{data.keyType}</Typography>
                  <Typography sx={{height:'41px',color:"white",width:"84%",borderRadius:"25px",margin:'auto',background:'#37BE70',fontWeight:'bold',fontSize:'22px'}}>{data.key}</Typography>
                </Box>
            </Card>
          </Box>
    )
  }
}
