import React, { Component } from 'react';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Image1 from '../assets/markerless4.png';
import Image2 from '../assets/markerless4.png';
import Image3 from '../assets/markerless2.png';
import Image4 from '../assets/markerless3.png';
import Image5 from '../assets/markerless1.png';
import CreateExperienceCard from '../components/cards/CreateExperienceCard';
import ExperiencesStepper from '../components/ExperienceCreationstepper/ExperiencesStepper';
import InputBase from '@mui/material/InputBase';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search'
import "../css/CreateExperiences.css"
import TextField from '@mui/material/TextField';

export default class CreateExperiences extends Component {
  constructor(props){
    super(props)
    this.state = {
      cardData:[{
        id:1,
        img:Image1,
        name:'Markerless AR',
        to:'/markerLessAR'
      },
      {
        id:2,
        img:Image2,
        name:'Markerbased AR',
        to:'/markerLessAR'
      },
      {
        id:3,
        img:Image3,
        name:'Face Tryon',
        to:'/markerLessAR'
      },
      {
        id:4,
        img:Image4,
        name:'Games',
        to:'/markerLessAR'
      },
      {
        id:5,
        img:Image5,
        name:'Workflow',
        to:'/markerLessAR'
      },
      ]
    }
  }

  render() {
    const {cardData} = this.state;
    return (
      <Box>
        <Box>
          <ExperiencesStepper />
        </Box>
       <Box
        sx={{
          borderRadius:'25px',
          color:'red',
          width:'80%',
          margin:'auto'
        }}
      >
        <TextField fullWidth label="Search" id="Search" 
          sx={{borderRadius:'25px'}}
        />
      </Box>
        <Grid container >
            {
              cardData.map((data)=>{
                return(                  
                  <Grid item xl={4} lg={6} md={6} sm={12}> 
                    <CreateExperienceCard data={data} />
                  </Grid>
                )
              })
            }
        </Grid>
      </Box>
    )
  }
}
