import React, { Component } from 'react'
import Box from '@mui/material/Box';
import { Button} from '@mui/material';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

export default class Payments extends Component {

  constructor(props){
    super(props)
    this.state  = {
      createData:(name, calories, fat, carbs, protein) =>{
        return { name, calories, fat, carbs, protein };
      }
    }
  }

  render() {

    const rows = [
      this.state.createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
      this.state.createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
      this.state.createData('Eclair', 262, 16.0, 24, 6.0),
      this.state.createData('Cupcake', 305, 3.7, 67, 4.3),
      this.state.createData('Gingerbread', 356, 16.0, 49, 3.9),
    ]
    return (
      <Box sx={{textAlign:'center'}}>
        <Typography sx={{fontSize:'35px'}} >
          Payment
        </Typography>
        <Typography>
          Step into endless possibilities
        </Typography>
        <Box sx={{p:5}}>
            <Button>All</Button>
            <Button >clear Filter</Button>
        </Box>
        <Box sx={{background:''}}>
          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell>Dessert (100g serving)</TableCell>
                  <TableCell align="right">Calories</TableCell>
                  <TableCell align="right">Fat&nbsp;(g)</TableCell>
                  <TableCell align="right">Carbs&nbsp;(g)</TableCell>
                  <TableCell align="right">Protein&nbsp;(g)</TableCell>
                </TableRow>
              </TableHead>
              <TableBody >
              
                {rows.map((row) => (
                  <TableRow
                    key={row.name}
                    sx={{height:'80px !important',borderRadius:'20px',border:'2px solid red',width:'100%'}}
                  >
                      <TableCell  >
                        {row.name}
                      </TableCell>
                      <TableCell align="right">{row.calories}</TableCell>
                      <TableCell align="right">{row.fat}</TableCell>
                      <TableCell align="right">{row.carbs}</TableCell>
                      <TableCell align="right">{row.protein}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>

        </Box>
      </Box>
    )
  }
}
