import React, { Component } from 'react'
// import ModelViewerElementBase from '@google/model-viewer/lib/model-viewer-base';
import Typography from '@mui/material/Typography';
import MagicPortal from '../../assets/education/MagicPortal.glb';
import Box from '@mui/material/Box';
import { Button } from '@mui/material';
import "../../css/ArGLBCard.css";


export default class ArGLBCard extends Component {

  constructor(props){
    super(props)
    this.state = {
      data:props.data,
      cardStyles : {
          visibility: 'hidden',
          '&:hover': {
            visibility: 'visible',
            }
        }
    }
  }

  render() {
    const data = this.state;
    return (
        <div id={"model"+data.id} style={{border:'1px solid lightgrey',borderRadius:'10px',width:"302px",height:'318px'}}>
            <Box className="GLBModal" sx={{height:'251px',display:'flex',alignItems:'center',borderRadius:'10px 10px 0px 0px',background:'#F1F3F5'}}>
                <model-viewer src={MagicPortal} sx={{display:'flex',alignItems:'center',justifyContent:'center'}} width="100%" height='251px' alt="A 3D model of a robot" auto-rotate="" camera-controls="" background-color="#455A64"></model-viewer>
            </Box>
        
            <Box sx={{position:'relative',bottom:'60px',width:'100%'}} >
              <Box sx={{display:'flex',justifyContent:'center'}}>
                <Button  id={"createARButton"+data.id} sx={{borderRadius:'25px',background:'#FF821C',width:'137px',height:'42px',color:'white',m:1,zIndex:10,opacity:53}}>Create AR</Button>
              </Box>
              <Box sx={{p:1}}>
                <Typography sx={{fontSize:'18px',fontWeight:'medium'}}>
                  Woman in saree
                </Typography>
                <Typography variant="subtitle1" sx={{fontSize:'16px',color:'grey'}}>
                  Ideal for markerless and workflow
                </Typography>
              </Box>
            </Box>
        </div>
    )
  }
}
