import React, { Component } from 'react'
import Box from '@mui/material/Box';
import AnalyticsChart from '../components/chart/AnalyticsChart';
import Button from '@mui/material/Button';
import { Typography } from '@mui/material';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Fade from '@mui/material/Fade';
import ArrowRightIcon from '@mui/icons-material/ArrowRight';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import "../css/Analytics.css";

export default class Analytics extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ExperienceOption:null,
            ExperienceOpen:false,
            SortByDateOption:null,
            SortByDateOpen:false,
            SortByGeographyOption:null,
            SortByGeographyOpen:false,
            TableData:[{
                    'Date':'22/10/2022',
                    'ViewCount':5454,
                    'SessionDuration':'20sec',
                    'ShareCount':302
                  },{
                    'Date':'22/10/2022',
                    'ViewCount':5454,
                    'SessionDuration':'20sec',
                    'ShareCount':302
                  },{
                    'Date':'22/10/2022',
                    'ViewCount':5454,
                    'SessionDuration':'20sec',
                    'ShareCount':302
                  },{
                    'Date':'22/10/2022',
                    'ViewCount':5454,
                    'SessionDuration':'20sec',
                    'ShareCount':302
                  },{
                    'Date':'22/10/2022',
                    'ViewCount':5454,
                    'SessionDuration':'20sec',
                    'ShareCount':302
                  },{
                    'Date':'22/10/2022',
                    'ViewCount':5454,
                    'SessionDuration':'20sec',
                    'ShareCount':302
                  },{
                    'Date':'22/10/2022',
                    'ViewCount':5454,
                    'SessionDuration':'20sec',
                    'ShareCount':302
                  },
                ],
            handleExperienceOpen:(event) =>{
                console.log("hello")
                this.setState({ExperienceOpen:true})
                this.setState({ExperienceOption:event.currentTarget})
            },
            handleExperienceClose:(event)=>{
                this.setState({ExperienceOpen:false})
                this.setState({ExperienceOption:null})
            },
            handleSortByDateOpen:(event) =>{
                console.log("from sort by date")
                this.setState({SortByDateOpen:true})
                this.setState({SortByDateOption:event.currentTarget})
            },
            handleSortByDateClose:(event)=>{
                this.setState({SortByDateOpen:false})
                this.setState({SortByDateOption:null})
            },
            handleSortByGeographyOpen:(event) =>{
                console.log("from handle sort by geography")
                this.setState({SortByGeographyOpen:true})
                this.setState({SortByGeographyOption:event.currentTarget})
            },
            handleSortByGeographyClose:(event)=>{
                this.setState({SortByGeographyOpen:false})
                this.setState({SortByGeographyOption:null})
            }
        }
    }
  
  
    render() {
    return (
        <Box >
            <Box sx={{textAlign:'center',color:'black'}}>
                <Typography sx={{fontSize:'35px'}}>Analytics</Typography>
                <Typography sx={{fontSize:'16px'}}>Step into endless possibilities</Typography>
            </Box>
            <Box sx={{display:'flex',justifyContent:'center',margin:'auto',p:10,color:'black'}}>
                <Box sx={{mr:4}}>
                <Button
                    id="fade-button"
                    aria-controls={this.state.ExperienceOpen ? 'fade-menu' : undefined}
                    aria-haspopup="true"
                    aria-expanded={this.state.ExperienceOpen ? 'true' : undefined}
                    onClick={this.state.handleExperienceOpen}
                    sx={{background:'#F1F3F5',borderRadius:'25px',width:'140px',color:'black',border:'1px solid lightGrey'}}

                    endIcon={this.state.ExperienceOpen ? <ArrowDropDownIcon />:<ArrowRightIcon />}
                >
                    Experience
                </Button>
                <Menu
                    id="fade-menu"
                    MenuListProps={{
                    'aria-labelledby': 'fade-button',
                    }}
                    anchorEl={this.state.ExperienceOption}
                    open={this.state.ExperienceOpen}
                    onClose={this.state.handleExperienceClose}
                    TransitionComponent={Fade}
                >
                    <MenuItem onClick={this.state.handleExperienceClose}>Markerless</MenuItem>
                    <MenuItem onClick={this.state.handleExperienceClose}>Workflow</MenuItem>
                    <MenuItem onClick={this.state.handleExperienceClose}>Games</MenuItem>
                    <MenuItem onClick={this.state.handleExperienceClose}>Tryon</MenuItem>
                    
                    <MenuItem onClick={this.state.handleExperienceClose} sx={{color:'red'}}>Close</MenuItem>
                </Menu>
                </Box>
                
                <Button id='GraphOperationButton' sx={{background:'#682CEA',color:'black',borderRadius:'25px',width:'80px',border:'1px solid #682CEA',}} >All</Button>
                
                {/* sort by Date*/}
                <Box sx={{ml:4}}>
                    <Button
                        id="fade-button"
                        aria-controls={this.state.SortByDateOpen ? 'fade-menu' : undefined}
                        aria-haspopup="true"
                        aria-expanded={this.state.SortByDateOpen ? 'true' : undefined}
                        onClick={this.state.handleSortByDateOpen}
                        sx={{background:'#F1F3F5',borderRadius:'25px',width:'160px',color:'black',border:'1px solid lightGrey'}}

                        endIcon={this.state.SortByDateOpen ? <ArrowDropDownIcon />:<ArrowRightIcon />}
                    >
                        Sort By Date
                    </Button>
                    <Menu
                        id="fade-menu"
                        MenuListProps={{
                        'aria-labelledby': 'fade-button',
                        }}
                        anchorEl={this.state.SortByDateOption}
                        open={this.state.SortByDateOpen}
                        onClose={this.state.handleSortByDateClose}
                        TransitionComponent={Fade}
                    >
                        <MenuItem onClick={this.state.handleSortByDateClose}>Current Year</MenuItem>
                        <MenuItem onClick={this.state.handleSortByDateClose}>Currnet Month</MenuItem>
                        <MenuItem onClick={this.state.handleSortByDateClose}>Current Week</MenuItem>
                        <MenuItem onClick={this.state.handleSortByDateClose}>Custom Range</MenuItem>
                    </Menu>
                </Box>
                
                {/* sort by geography*/}
                <Box sx={{ml:4}}>
                    <Button
                        id="fade-button"
                        aria-controls={this.state.SortByGeographyOpen ? 'fade-menu' : undefined}
                        aria-haspopup="true"
                        aria-expanded={this.state.SortByGeographyOpen ? 'true' : undefined}
                        onClick={this.state.handleSortByGeographyOpen}
                        sx={{background:'#F1F3F5',borderRadius:'25px',width:'200px',color:'black',border:'1px solid lightGrey'}}

                        endIcon={this.state.SortByGeographyOpen ? <ArrowDropDownIcon />:<ArrowRightIcon />}
                    >
                        Sort by Geography
                    </Button>
                    <Menu
                        id="fade-menu"
                        MenuListProps={{
                        'aria-labelledby': 'fade-button',
                        }}
                        anchorEl={this.state.SortByGeographyOption}
                        open={this.state.SortByGeographyOpen}
                        onClose={this.state.handleSortByGeographyClose}
                        TransitionComponent={Fade}
                    >
                        <MenuItem onClick={this.state.handleSortByGeographyClose}>India</MenuItem>
                        <MenuItem onClick={this.state.handleSortByGeographyClose}>America</MenuItem>
                        <MenuItem onClick={this.state.handleSortByGeographyClose}>Australia</MenuItem>
                    </Menu>
                </Box>

                <Button sx={{ml:4}}>Clear Filter</Button>
            </Box>
            <Box sx={{width:'80%',margin:'auto',p:4}}>
                <Typography variant='h5' sx={{margin:'auto',pl:2}}>
                    All
                </Typography>
            </Box>
            <Box sx={{margin:'auto',padding:3,border:'1px solid lightGrey',width:'1296px',borderRadius:'25px'}}>
               
                <AnalyticsChart />
            </Box>
            <Box style={{display:'flex',width:'90%',alignItems:'center',justifyContent:'center',margin:'auto'}}>
                <Box sx={{px:10,py:6,display:'flex',alignItems:'center'}}>
                    <Box sx={{width:'76px',height:'76px',background:'blue',borderRadius:'50px',boxShadow:'2px 2px 2px 2px grey'}}></Box>
                    <Box sx={{p:2}}>
                        <Typography sx={{font:'normal normal 600 20px/27px Noto Sans'}}>View Count</Typography>
                        <Typography sx={{fontSize:'32px',fontWeight:'bold'}}>5674</Typography>
                    </Box>
                </Box>
                <Box sx={{px:10,py:6,display:'flex',alignItems:'center'}}>
                    <Box sx={{width:'76px',height:'76px',background:'yellow',borderRadius:'50px',boxShadow:'2px 2px 2px 2px grey'}}></Box>
                    <Box sx={{p:2}}>
                        <Typography sx={{font:'normal normal 600 20px/27px Noto Sans' }} > Avg. Session Duration</Typography>
                        <Typography sx={{fontSize:'32px',fontWeight:'bold'}}>120sec</Typography>
                    </Box>
                </Box>
                <Box sx={{px:10,py:6,display:'flex',alignItems:'center'}}>
                    <Box sx={{width:'76px',height:'76px',background:'green',borderRadius:'50px',boxShadow:'2px 2px 2px 2px grey'}}></Box>
                    <Box sx={{p:2}}>
                        <Typography sx={{font:'normal normal 600 20px/27px Noto Sans'}}> Share Count</Typography>
                        <Typography sx={{fontSize:'32px',fontWeight:'bold'}}>34</Typography>
                    </Box>
                </Box>
            </Box>
            <Box style={{border:'1px solid lightgrey',width:"76%",textAlign:'center',margin:'auto',p:4,borderRadius:'10px'}}>
                <TableContainer sx={{p:2}}>
                <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                    <TableHead>
                        <TableRow>
                            <TableCell align="center" sx={{fontSize:'16px',fontWeight:'bold'}}>Date</TableCell>
                            <TableCell align="center" sx={{fontSize:'16px',fontWeight:'bold'}}>View Count</TableCell>
                            <TableCell align="center" sx={{fontSize:'16px',fontWeight:'bold'}}>Session Duration</TableCell>
                            <TableCell align="center" sx={{fontSize:'16px',fontWeight:'bold'}}>Share Count</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody sx={{}}>
                    {this.state.TableData.map((row) => (
                        <TableRow
                            key={row.name}
                            sx={{ '&:last-child td, &:last-child th': { border: 0 } ,height:'50px'}}
                        >
                            <TableCell align="center" sx={{fontSize:'16px'}}>{row.Date}</TableCell>
                            <TableCell align="center" sx={{fontSize:'16px'}}>{row.ViewCount}</TableCell>
                            <TableCell align="center" sx={{fontSize:'16px'}}>{row.SessionDuration}</TableCell>
                            <TableCell align="center" sx={{fontSize:'16px'}}>{row.ShareCount}</TableCell>
                        </TableRow>
                    ))}
                    </TableBody>
                </Table>
                </TableContainer>
            </Box>
        </Box>
    )
  }
}
