import React, { Component } from 'react';
import Box from '@mui/material/Box';
import PropTypes from 'prop-types';
import { styled } from '@mui/material/styles';
import Stack from '@mui/material/Stack';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Check from '@mui/icons-material/Check';
import SettingsIcon from '@mui/icons-material/Settings';
import GroupAddIcon from '@mui/icons-material/GroupAdd';
import VideoLabelIcon from '@mui/icons-material/VideoLabel';
import StepConnector, { stepConnectorClasses } from '@mui/material/StepConnector';

export default class ExperiencesStepper extends Component {

  constructor(props){
    super(props)
    this.state = {
      QontoConnector : styled(StepConnector)(({ theme }) => ({
        [`&.${stepConnectorClasses.alternativeLabel}`]: {
          top: 10,
          left: 'calc(-50% + 16px)',
          right: 'calc(50% + 16px)',
        },
        [`&.${stepConnectorClasses.active}`]: {
          [`& .${stepConnectorClasses.line}`]: {
            borderColor: '#784af4',
          },
        },
        [`&.${stepConnectorClasses.completed}`]: {
          [`& .${stepConnectorClasses.line}`]: {
            borderColor: '#784af4',
          },
        },
        [`& .${stepConnectorClasses.line}`]: {
          borderColor: theme.palette.mode === 'dark' ? theme.palette.grey[800] : '#eaeaf0',
          borderTopWidth: 3,
          borderRadius: 1,
        },
      })),
      QontoStepIconRoot : styled('div')(({ theme, ownerState }) => ({
        color: theme.palette.mode === 'dark' ? theme.palette.grey[700] : '#eaeaf0',
        display: 'flex',
        height: 22,
        alignItems: 'center',
        ...(ownerState.active && {
          color: '#784af4',
        }),
        '& .QontoStepIcon-completedIcon': {
          color: '#784af4',
          zIndex: 1,
          fontSize: 18,
        },
        '& .QontoStepIcon-circle': {
          width: 8,
          height: 8,
          borderRadius: '50%',
          backgroundColor: 'currentColor',
        },
      })),

      // QontoStepIcon:(event) => {
      //   const { active, completed, className } = event;
      //   // const active='true';
      //   // const completed = 'None';
      //   // const className = 'Nonne';
      //   return (
      //     <this.QontoStepIconRoot ownerState={{ active }} className={className}>
      //       {completed ? (
      //         <Check className="QontoStepIcon-completedIcon" />
      //       ) : (
      //         <div className="QontoStepIcon-circle" />
      //       )}
      //     </this.QontoStepIconRoot>
      //   );
      // }
    }
  }

  render() {  
    const {QontoConnector,QontoStepIcon} = this.state
    const steps = ['', '', '']
    return (
      <Box sx={{px:5,pb:4}}>
        <Stack sx={{ width: '100%' }} spacing={4}>
          <Stepper alternativeLabel activeStep={0} connector={<QontoConnector />}>
            {steps.map((label) => {
              return(
              <Step >
                <StepLabel  ></StepLabel>
              </Step>)
              }
            )}
          </Stepper>
        </Stack>
      </Box>
    );
  }
}
