import React, { Component } from 'react'
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import { Line } from 'react-chartjs-2';
import Chart from 'chart.js/auto';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { Button } from '@mui/material';
export default class AnalyticsChart extends Component {


    constructor(props) {
        super(props);
        this.state = {
            Duration:'',
            handleChange : (event) => {
                this.setState({Duration:event.target.value});
            }
        }
      }
      componentDidMount(){
      }

      render(){
        
        const labels  = ['date1', 'date2', 'date3','date4', 'date5', 'date6','date7', 'date8', 'date9','date10', 'date11', 'date12']
        // console.log(faker)
        const ColorItems = [{
            name:'View Count',
            color:'rgb(255, 99, 132)'
          },{
            name:'Share',
            color:'rgb(255, 99, 132)'
          },{
            name:'Session Duration',
            color:'rgb(255, 99, 132)'
          }]
        return(
            <Box style={{
                width:"100%",
                maxHeight:"100%",
            
            }}>
                <Box style={{display:'flex',marginTop:'10px'}}>
                    <Box sx={{width:'85%',display:'flex',mt:2}}>
                    {
                        ColorItems.map((Item)=>{
                            return(<Box sx={{ml:4,display:'flex'}}>
                                    <div style={{padding:'4px',border:"5px solid white",alignItems:'center',justifyContent:"center",borderRadius:'50px',height:'10px',width:'10px',background:`${Item.color}` }}> </div>
                                    <Typography sx={{padding:'1px'}}>{Item.name}</Typography>
                                </Box>)  
                        })
                    }
                    </Box>
                     <Box sx={{justifyContent:'right !important',width:'15%',height:'20px',mt:1}}>
                        {/* <Button style={{background:'blue'}}>
                            DropDown
                        </Button> */}
                        <Box style={{width:'120px'}} >
                            <FormControl fullWidth sx={{height:'10px !important'}}>
                                <InputLabel id="demo-simple-select-label">Sort By</InputLabel>
                                <Select
                                height="100%"
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                value={this.state.Duration}
                                label="Yearly"
                                onChange={this.state.handleChange}
                                >
                                <MenuItem value={"yearly"}>Year</MenuItem>
                                <MenuItem value={"monthly"}>Month</MenuItem>
                                <MenuItem value={"weekly"}>Week</MenuItem>
                                </Select>
                            </FormControl>
                        </Box>
                    </Box>
                </Box>
               
                <Line
                height={100}
                data={{
                        labels: labels,
                        datasets: [
                            {
                                label: 'Dataset 1',
                                data:[55,23,12,54,23,187,55,23,12,54,23,187], 
                                fill: true,
                                // labels.map(() => faker.datatype.number({ min: -1000, max: 1000 })),
                                borderColor: 'rgb(255, 99, 132)',
                                // backgroundColor: 'rgba(255, 99, 132, 0.5)',
                                backgroundColor: 'transparent',
                                tension: 0.5
                            },
                            {
                                label: 'Dataset 2',
                                data: [22,4,3,11,21,1,66,55,23,12,54,23,187],
                                // labels.map(() => faker.datatype.number({ min: -1000, max: 1000 })),
                                fill: true,
                                borderColor: 'rgb(53, 162, 235)',
                                backgroundColor: 'transparent',
                                tension: 0.5
                            },
                            {
                                label: 'Dataset 3',
                                data:[20,11,34,4,4,2,96,55,123,12,544,23,187],
                                fill: true,
                                //  labels.map(() => faker.datatype.number({ min: -1000, max: 1000 })),
                                borderColor: 'rgb(255,255,0)',
                                backgroundColor: 'transparent',
                                tension: 0.5
                            },
                        ]
                    }}
                options={{
                    plugins: {
                        title: {
                        display: true,
                        text: ""
                        },
                        legend: {
                        display: true,
                        position: "bottom"
                        }
                    },
                    scales: {
                        x: {
                            grid: {
                                display: false      
                            }
                        },
                        y: {
                            grid: {
                                display: false      
                            }
                        }
                    }
                }}
                />
            </Box>
        
    )}

}