import React, { Component } from 'react'
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import testImage from '../../assets/test.png'
import { Button, CardActionArea, CardActions } from '@mui/material';

export default class VideoCard extends Component {

  constructor(props){
    super(props)
    // console.log("my wikiCard props",props.CardData);
    this.state= {
      id:this.props.CardData.id,
      Image:this.props.CardData.img,
      HeadingText:this.props.CardData.HeadingText,
      ParagraphText:this.props.CardData.ParagraphText,
      SendtoPage:this.props.CardData.to,
    }
  }

  render() {
    const {Id,Image,HeadingText,ParagraphText,SendtoPage} = this.state;
      // console.log(HeadingText)
      // console.log(ParagraphText)
    return (
      <Box style={{p:1,minHeight:'469px'}}>
        <Card sx={{ Width: "100%",minHeight: '465px' }} key={Id}>
          <CardActionArea style={{background:''}}>
            <CardMedia
              sx={{borderRadius:'5px'}}
              component="img"
              height="251px"
              image={Image}
              alt="green iguana"
            />
            <CardContent>
              <Typography style={{fontWeight:'bold'}} gutterBottom variant="h5" component="div">
                {HeadingText}
              </Typography>
              <Typography variant="body2" sx={{fontSize:'16px'}} color="text.secondary">    
                {ParagraphText}
              </Typography>
            </CardContent>
            <CardActions sx={{justifyContent:'center',mt:4}}>
              <Button  size="small" color="primary" to={SendtoPage}>
                See more experiences 
              </Button>
            </CardActions>
          </CardActionArea>
        </Card>
      </Box>
    )
  }
}
